"use client"

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Search, MapPin, Heart } from 'lucide-react'
import { useState } from "react"
import { useRouter } from 'next/navigation'
import { useCity } from "@/context/city-context"

export function HeroSection() {
  const [searchQuery, setSearchQuery] = useState("")
  const [selectedCity, setSelectedCity] = useState("Delhi")
  const router = useRouter()
  const { setSelectedCity: setCityContext } = useCity()

  const cities = ["Delhi", "Mumbai", "Bangalore", "Hyderabad", "Chennai", "Goa", "Gurugram"]

  const handleSearch = () => {
    if (searchQuery.trim()) {
      setCityContext(selectedCity)
      router.push(`/movies?search=${encodeURIComponent(searchQuery)}`)
    } else {
      setCityContext(selectedCity)
      router.push("/movies")
    }
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSearch()
    }
  }

  return (
    <div className="relative bg-gradient-to-br from-primary via-primary to-primary/80 py-12 md:py-20">
      {/* Background Pattern */}
      <div className="absolute inset-0 opacity-10">
        <div className="absolute top-10 right-10 w-72 h-72 bg-accent rounded-full blur-3xl"></div>
        <div className="absolute bottom-10 left-10 w-72 h-72 bg-accent/50 rounded-full blur-3xl"></div>
      </div>

      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold text-primary-foreground mb-4 text-balance">
            Book Your Next Movie Experience
          </h1>
          <p className="text-lg md:text-xl text-primary-foreground/80 max-w-2xl mx-auto">
            Discover and book tickets for movies, events, and activities at the best prices
          </p>
        </div>

        {/* Search Bar */}
        <div className="bg-white rounded-2xl shadow-2xl p-6 md:p-8 max-w-3xl mx-auto">
          <div className="grid md:grid-cols-2 gap-4 mb-6">
            {/* Location Selector */}
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Select City</label>
              <div className="flex items-center gap-2 px-4 py-3 border border-border rounded-lg bg-card">
                <MapPin className="w-5 h-5 text-accent" />
                <select 
                  value={selectedCity}
                  onChange={(e) => setSelectedCity(e.target.value)}
                  className="flex-1 bg-transparent font-medium text-foreground focus:outline-none"
                >
                  {cities.map((city) => (
                    <option key={city} value={city}>{city}</option>
                  ))}
                </select>
              </div>
            </div>

            {/* Search Input */}
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Search Movie</label>
              <div className="flex items-center gap-2 px-4 py-3 border border-border rounded-lg bg-card">
                <Search className="w-5 h-5 text-muted-foreground" />
                <input
                  type="text"
                  placeholder="Search movies, events..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  onKeyPress={handleKeyPress}
                  className="flex-1 bg-transparent font-medium text-foreground placeholder:text-muted-foreground focus:outline-none"
                />
              </div>
            </div>
          </div>

          <div className="flex flex-col sm:flex-row gap-3">
            <Button 
              onClick={handleSearch}
              className="flex-1 bg-accent hover:bg-accent/90 text-accent-foreground font-bold h-12 text-lg rounded-lg"
            >
              Search Now
            </Button>
            <Link href="/favorites" className="flex-1">
              <Button 
                variant="outline"
                className="w-full border-accent text-accent hover:bg-accent/10 font-bold h-12 text-lg rounded-lg flex items-center justify-center gap-2"
              >
                <Heart className="w-5 h-5" />
                My Favorites
              </Button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  )
}
